
.wrapper {
  position: relative;
  margin: 0.2em;
  background: white;
}
.wrapper, .wrapper * {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
.wrapper {
  width: 30px;
  height: 30px;
}
.wrapper .pie {
  width: 50%;
  height: 100%;
  -webkit-transform-origin: 100% 50%;
          transform-origin: 100% 50%;
  position: absolute;
  background: #fc6d26;
  border: 5px solid #fca326;
}
.wrapper .spinner {
  border-radius: 100% 0 0 100% / 50% 0 0 50%;
  z-index: 200;
  border-right: none;
}
.wrapper:hover .spinner,
.wrapper:hover .filler,
.wrapper:hover .mask {
  -webkit-animation-play-state: running;
          animation-play-state: running;
}
.wrapper .filler {
  border-radius: 0 100% 100% 0 / 0 50% 50% 0;
  left: 50%;
  opacity: 0;
  z-index: 100;
  border-left: none;
}
.wrapper .mask {
  width: 50%;
  height: 100%;
  position: absolute;
  background: inherit;
  opacity: 1;
  z-index: 300;
}
@-webkit-keyframes rota {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes rota {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@-webkit-keyframes opa {
0% {
    opacity: 1;
}
50%, 100% {
    opacity: 0;
}
}
@keyframes opa {
0% {
    opacity: 1;
}
50%, 100% {
    opacity: 0;
}
}



